<template>
  <div class="modal animated fadeIn" :class="{ 'is-active':isActive }">
    <div @click="closeModal" class="modal-background"></div>
    <div class="modal-content">
      <div class="card">
        <div class="card-content">
          <div class="media media-left">
            <div class="media-left">
              <span>{{ emoji }}</span>
            </div>
            <div class="media-content">
              <p class="title is-1">{{ translation }}</p>
            </div>
          </div>
          <div class="content">
            <a>#{{ category }}</a>
          </div>
        </div>
      </div>
    </div>
    <button @click="closeModal" class="modal-close is-large" aria-label="close"></button>
  </div>
</template>

<script>
export default {
  name: 'word-modal',
  props: {
    isActive: Boolean,
    translation: String,
    emoji: String,
    category: String,
  },
  methods: {
    closeModal() {
      this.$emit('update:isActive', false);
    },
  },
};
</script>

<style scoped>
span {
  font-size: 3rem;
}
div {
  font-size: 1.5rem;
}
</style>


