import Vue from 'vue';
import VueRouter from 'vue-router';

import Index from '@/pages/Index';
import Language from '@/pages/Language';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index,
  },
  {
    path: '/languages/:name',
    name: 'language',
    component: Language,
  },
];

const router = new VueRouter({
  routes,
});

export default router;



