<template>
  <div>
    <word-info
      :wordCount="wordCount"
      :translatedWordCount="translatedWordCount"
    ></word-info>

    <word-category
      v-for="category in categories"
      v-bind:key="category.name"
      :name="category.name"
      :words="category.words"
      :language="language"
    ></word-category>
  </div>
</template>

<script>
import wordsData from '@/data/words.json';
import WordInfo from '@/components/WordInfo';
import WordCategory from '@/components/WordCategory';

export default {
  name: 'language',
  components: {
    WordInfo,
    WordCategory,
  },
  computed: {
    language() {
      return this.$route.params.name;
    },
    categories() {
      const all = [];
      wordsData.forEach((word) => {
        const wordCategory = word.category;
        const category = all.find(({ name }) => name === wordCategory);
        if (!category) {
          all.push({
            name: wordCategory,
            words: [word],
          });
        } else {
          category.words.push(word);
        }
      });
      return all;
    },
    wordCount() {
      /* eslint-disable no-param-reassign */
      /* eslint-disable no-unused-vars */
      /* eslint-disable no-return-assign */
      return wordsData.reduce((count, word) => count += 1, 0);
    },
    translatedWordCount() {
      const choosedLanguage = this.language;
      return wordsData.reduce((total, { translations }) => {
        if (translations[choosedLanguage]) {
          return total += 1;
        }
        return total;
      }, 0);
    },
  },
};
</script>

<style>
</style>


