<template>
  <main class="container">
    <language-picker
      v-model="language"
      :languages="languages"
      :placeholder="'Select language'"
    ></language-picker>
    <router-view></router-view>
  </main>
</template>

<script>
import languageData from '@/data/languages.json';
import LanguagePicker from '@/components/LanguagePicker';

export default {
  name: 'app-main',
  components: {
    LanguagePicker,
  },
  data: () => ({
    language: '',
  }),
  watch: {
    language() {
      const languageUrl = this.language ? `/languages/${this.language}` : '';
      this.$router.push(languageUrl);
    },
  },
  computed: {
    languages() {
      return Object.entries(languageData).map((language) => {
        const [slug, { name, nativeName }] = language;
        return {
          slug,
          name,
          nativeName,
        };
      });
    },
  },
};
</script>

<style scoped>
.container {
  width: 90%;
}
</style>


